import { IFormValidator, IFormValidatorOptions, IFormValidatorResponseItem } from "../domain/infrastructure/IFormValidator";


export class FormValidatorImpl implements IFormValidator {
    public validate(value: string, options: IFormValidatorOptions) {
        const retVal: Array<IFormValidatorResponseItem> = []

        if(options.maxLength){
            if(!this.checkMaxLength(value, options.maxLength.length)){
                retVal.push({key: "maxLength", message: options.maxLength.message})
            }
        }
        if(options.minLength){
            if(!this.checkMinLength(value, options.minLength.length)){
                retVal.push({key: "maxLength", message: options.minLength.message})
            }
        }

        return retVal;
    }

    private checkMaxLength(value: string, length: number): boolean{
        return value.length <= length;    
    }

    private checkMinLength(value: string, length: number): boolean{
        return value.length >= length;    
    }
}